// Layout Properties
$grid-base: 4px;
$grid-base-1x: $grid-base;
$grid-base-2x: $grid-base * 2;
$grid-base-3x: $grid-base * 3;
$grid-base-4x: $grid-base * 4;
$grid-base-5x: $grid-base * 5;
$grid-base-6x: $grid-base * 6;
$grid-standard: $grid-base-3x;
$grid-block-spacing: $grid-standard * 2;

// Font Sizes
$font-small-3: 8px;
$font-small-2: 10px;
$font-small-1: 12px;
$font-standard: 14px;
$font-normal-1: 16px;
$font-normal-2: 18px;
$font-big: 20px;
$font-big-1: 24px;
$font-big-2: 30px;
$font-title: 36px;
$font-title-1: 48px;
$font-title-2: 60px;
$font-maxima: 72px;

// Font Weights
$weight-hairline: 100;
$weight-light: 300;
$weight-normal: 400;
$weight-medium: 500;
$weight-heavy: 600;
$weight-bold: 700;
$weight-black: 900;

// Border Properties
$border-radius-standard: $grid-base;
$border-radius-small: $grid-base / 2;
$border-radius-1: 5px;
$border-radius-2: 6px;

// Timings
$animation-standard: 0.25s;

// Easings
$easing-smooth-ease-out: cubic-bezier(0.4, 1.03, 0.29, 0.99);

// Animations
$smooth-ease-out: ($animation-standard * 3) $easing-smooth-ease-out;
$quick-ease-out: $animation-standard $easing-smooth-ease-out;

// Colors
$blue-dark-4: hsl(205, 100%, 21%);
$blue-dark-3: hsl(205, 87%, 29%);
$blue-dark-2: hsl(205, 82%, 33%);
$blue-dark-1: hsl(205, 76%, 39%);
$blue-standard: hsl(205, 67%, 45%);
$blue-light-1: hsl(205, 65%, 55%);
$blue-light-2: hsl(205, 74%, 65%);
$blue-light-3: hsl(205, 84%, 74%);
$blue-light-4: hsl(205, 97%, 85%);
$blue-light-5: hsl(205, 79%, 92%);

$yellow-dark-4: hsl(15, 86%, 30%);
$yellow-dark-3: hsl(22, 82%, 39%);
$yellow-dark-2: hsl(29, 80%, 44%);
$yellow-dark-1: hsl(36, 77%, 49%);
$yellow-standard: hsl(42, 87%, 55%);
$yellow-light-1: hsl(44, 92%, 63%);
$yellow-light-2: hsl(48, 94%, 68%);
$yellow-light-3: hsl(48, 95%, 76%);
$yellow-light-4: hsl(48, 100%, 88%);
$yellow-light-5: hsl(49, 100%, 96%);

$blue-grey-dark-4: hsl(209, 61%, 16%);
$blue-grey-dark-3: hsl(211, 39%, 23%);
$blue-grey-dark-2: hsl(209, 34%, 30%);
$blue-grey-dark-1: hsl(209, 28%, 39%);
$blue-grey-standard: hsl(210, 22%, 49%);
$blue-grey-light-1: hsl(209, 23%, 60%);
$blue-grey-light-2: hsl(211, 27%, 70%);
$blue-grey-light-3: hsl(210, 31%, 80%);
$blue-grey-light-4: hsl(212, 33%, 89%);
