$cyan-dark-4: hsl(184, 91%, 17%);
$cyan-dark-3: hsl(185, 84%, 25%);
$cyan-dark-2: hsl(185, 81%, 29%);
$cyan-dark-1: hsl(184, 77%, 34%);
$cyan-standard: hsl(185, 62%, 45%);
$cyan-light-1: hsl(185, 57%, 50%);
