$red-dark-1: hsl(360, 72%, 38%);
$red-standard: hsl(360, 67%, 44%);
$red-light-1: hsl(360, 64%, 55%);
$red-light-2: hsl(360, 71%, 66%);
$red-light-3: hsl(360, 77%, 78%);
$red-light-4: hsl(360, 82%, 89%);
