$greyscale-dark-4: #303030;
$greyscale-dark-3: #686868;
$greyscale-dark-2: #8d8d8d;
$greyscale-dark-1: #a4a4a4;
$greyscale-standard: #b6b6b6;
$greyscale-light-1: #cbcbcb;
$greyscale-light-2: #e0e0e0;
$greyscale-light-3: #f0f0f0;
$greyscale-light-4: #f3f3f3;
$greyscale-light-5: #fafafa;

$gray-black: #4a4a4a;

// Generic Colour Set
$green-1: #219653;
$green-2: #27ae60;
$green-3: #6fcf97;

// MARK: !!DEPRECATED!! Design specific colours
$white: rgb(255, 255, 255);
$butterscotch: rgb(250, 182, 80);
$pumpkin-orange: rgb(250, 117, 40);
$orange-butterscotch: rgb(251, 150, 60);
$mango-two: rgb(255, 191, 50);
$light-orange: rgb(248, 165, 64);
$light-navy: rgb(20, 78, 147);
$weird-green: rgb(69, 233, 148);
$tealish: rgb(35, 188, 186);
$turquoise-blue: rgb(0, 173, 183);
$topaz: rgb(21, 206, 171);
$light-blue-grey: rgb(228, 247, 248);
$warm-grey: rgb(128, 128, 128);

// Shadows
$box-shadow-butterscotch: 0 4px 10px 0 rgba(248, 165, 64, 0.5);
$box-shadow-teal: 0 4px 10px 0 $turquoise-blue;

//Gradients
$gradient-orangeish: linear-gradient(285deg, $pumpkin-orange, $butterscotch);
$gradient-orangeish-flipped: linear-gradient(115deg, $pumpkin-orange, $butterscotch);
$gradient-greenish: linear-gradient(255deg, $tealish, $weird-green);

// Z-Indices
$z--base: 0;
$z--one: 1;
$z--search-overlay: 49;
$z--search: 50;
$z--modal: 100;
$z--toast: 20000;
$z--toast--active: 20001;
$z--maximum: 2147483647;

// Letter Spacings
$ls-standard: 0.5px;

// Box Shadows
@mixin shadow-1 {
  box-shadow: 0 $grid-base $grid-base rgba(0, 0, 0, 0.25);
}
@mixin shadow-2 {
  box-shadow: 0 $grid-base $grid-base-3x rgba(0, 0, 0, 0.25), 0 2px $grid-base rgba(0, 0, 0, 0.25);
}
@mixin shadow-3 {
  box-shadow: 0 $grid-base-2x $grid-base-6x rgba(0, 0, 0, 0.2), 0 2px $grid-base-2x rgba(0, 0, 0, 0.12);
}

// Selectors
$mui-element: ">div[class^='Mui'], >div[class^='jss']";
